* {
  font-family: 'Lucida Sans', Arial, sans-serif;
}


.nav {
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding: 5px 0px;
  width: 100%;
  background-image:
  linear-gradient(to bottom, rgba(19, 19, 19, 0.52), rgba(5, 46, 0, 0.2)),
  url('./food.JPG');
}
.nav .title {
  text-transform: uppercase;
  font-size: 1.6em;
  color: white;
}

.nav .input {
  height: 35px;
  border: none;
  outline: none;
  background: rgba(1, 20, 0, 0.9);
  color: rgb(135, 197, 65);
  padding: 10px;
}

.userinputtitle {
  text-align: center;
  text-transform: uppercase;
  background: rgb(135, 197, 65);
  color: white;
  width: 100%;
  margin: 0px;
  padding: 10px 0px;
  color: black;
}

.nav button {
  text-transform: uppercase;
  height: 35px;
  border: none;
  width: 75px;
  background: rgb(135, 197, 65);
}

/* Mainjs */

.containrecipes {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  animation: ease-in 0.4s all;
  animation-name: fades;
}

.getrecipes {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  flex-wrap: wrap;
  flex: 1;
  padding: 12px;
  margin: 0px 10px;
  animation: ease-in 0.4s all;
  animation-name: fades;
}

.recipetitle {
    width: 80%;
    font-size: 1.7em;
    -webkit-text-fill-color: transparent;
    -webkit-text-stroke-width: 1px;
    -webkit-text-stroke-color: rgb(135, 197, 65);
}

h4 {
  margin: 0px;
  padding: 8px 0px;
}

.health {
  width: 70%;
}

.cautions {
  margin: 0px;
}

.ingredients {
  list-style-type: none;
  padding: 0;
  margin: 0;
  /* width: 90%; */
}

.ingredient {
  padding: 5px 0px;
}


img {
  border-radius: 10px;
  box-shadow: rgba(0, 0, 0, 0.589) 1px 1px 5px;
}


.getingredients {
  height: 40px;
  width: 40%;
  border: none;
  outline: none;
  text-transform: uppercase;
  background: rgb(135, 197, 65);
  transition: ease-in 0.3s all;
}

.getingredients:hover {
  cursor: pointer;
  background: black;
  color: rgb(135, 197, 65);
}

.containingredients {
  text-align: center;
  display: none;
}



@media only screen and (max-width: 415px) {

  .nav {
    padding: 15px 0px;
  }
  .nav .title {
    display: none
  }

  img {
    width: 225px;
    height: 200px;
  }

  .recipetitle {
    font-size: 1.4em;
  }

}


@keyframes fades {
  0% {opacity: 0; transform: translateX(20px);}
  100% {opacity: 1; transform: translateX(0px);}
}